import { StyledImage } from "./styles";
import { ProfileBlockProps } from "../types";

export const ProfileBlock = ({ src, width, height, link }: ProfileBlockProps) => (
  <a href={link} target='_blank' rel="noreferrer">
    <StyledImage src={`/img/contents/${src}`} alt={src} width={width} height={height} />
  </a>
);